$black: #301;

body {
  margin: 0;
  font-size: 0;
}

#colorcontainer, #sizecontainer {
  position: absolute;
  bottom: 4px;
  left: 60px;
  display: flex;
  align-items: center;
  transform-origin: -35px center;
  transform: rotate(-180deg);
  transition: transform .2s ease-in-out;
  height: 42px;
}

#brushcontainer {
  position:absolute;
  bottom: 60px;
  left: 4px;
  display: flex;
  flex-direction: column;
  transform-origin: center calc(100% + 35px);
  transform: rotate(-90deg);
  transition: transform .2s ease-in-out;
}

.colorpicker {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  box-sizing: border-box;
  margin: 2px ;
  cursor: pointer;
  position: relative;
  border: 2px solid $black;
}

.brushpicker {
  margin: 5px;
}

.sizepicker{
  border-radius: 50%;
  background-color: $black;
  margin: 5px;
}

#menu {
  position: absolute;
  left: 8px;
  bottom: 8px;
  &.active { 
    #colorcontainer{
      transform: rotate(0);
    }
    #brushcontainer   {
      transform: rotate(0);
    }
    #sizecontainer {
      transform: rotate(-45deg);
    }
  }
}

#menubutton {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: $black;
  border: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
}

#clear, #save {
  font-size: 20px;
  margin-bottom: 10px;
}
.tools{
  margin: 5px 5px 16px;
}